import { message } from "antd";
import React, { Component, createRef } from "react";
import { connect } from "react-redux";
import { capNhatDanhSachMonAnAction } from "../Redux/Action/food.action";
import { CAP_NHAT_MON_AN } from "../Redux/Constant/food.constant";
import { monAnService } from "../Service/food.service";

class FoodForm extends Component {
  state = {
    name: "",
    price: "",
    img: "",
    description: "",
  };

  constructor(props) {
    super(props);
    // this.nameRef = createRef();
    // this.priceRef = createRef();
    this.formRef = createRef();
  }

  componentDidMount() {
    // this.nameRef.current.focus();
    // this.priceRef.current.focus();
    this.formRef.current.reset();
  }

  handleOnChange = (e) => {
    let name = e.target.name;
    let value = e.target.value;
    this.setState({ [name]: value });
  };

  // Promise chaining
  handleThemMonAn = () => {
    monAnService
      .themMonAn(this.state)
      .then((res) => {
        return monAnService.layDanhSach();
      })

      .then((res) => {
        this.props.capNhatDanhSachMonAn(res.data);
        message.success("Thm mn n thnh cng");
        console.log(res);
      })

      .catch((err) => {
        message.error("Thm mn n tht bi");
        console.log(err);
      });
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    // console.log("nextProps", nextProps);
    if (nextProps.foodEdit) {
      this.setState({
        ...nextProps.foodEdit,
      });
    }
  }

  handleCapNhatMonAn = (id) => {
    monAnService
      .capNhatMonAn(id)
      .then((res) => {
        return monAnService.layDanhSach();
      })

      .then((res) => {
        this.props.capNhatDanhSachMonAn(res.data);
        message.success("Cp nht mn n thnh cng");
        console.log(res);
      })

      .catch((err) => {
        message.error("Cp nht mn n tht bi");
        console.log(err.res.data.err);
      });
  };

  render() {
    return (
      <div className="container text-left my-5">
        <form className="row mx-0" ref={this.formRef}>
          <div className="form-group col-6">
            <label className="font-weight-bold" htmlFor="name">
              Tn mn:
            </label>
            <input
              // ref={this.nameRef}
              type="text"
              value={this.state.name}
              className="form-control"
              name="name"
              id="name"
              aria-describedby="helpId"
              placeholder="Nhp tn mn"
              onChange={(event) => {
                this.handleOnChange(event);
              }}
            />
          </div>
          <div className="form-group col-6">
            <label className="font-weight-bold" htmlFor="price">
              Gi mn:
            </label>
            <input
              // ref={this.priceRef}
              type="text"
              value={this.state.price}
              className="form-control"
              name="price"
              id="price"
              aria-describedby="helpId"
              placeholder="Nhp tn mn"
              onChange={(event) => {
                this.handleOnChange(event);
              }}
            />
          </div>
          <div className="form-group col-6">
            <label className="font-weight-bold" htmlFor="img">
              Hnh nh mn:
            </label>
            <input
              type="text"
              value={this.state.img}
              className="form-control"
              name="img"
              id="img"
              aria-describedby="helpId"
              placeholder="Nhp tn mn"
              onChange={(event) => {
                this.handleOnChange(event);
              }}
            />
          </div>
          <div className="form-group col-6">
            <label className="font-weight-bold" htmlFor="name">
              M t mn:
            </label>
            <input
              type="text"
              value={this.state.description}
              className="form-control"
              name="description"
              id="description"
              aria-describedby="helpId"
              placeholder="Nhp tn mn"
              onChange={(event) => {
                this.handleOnChange(event);
              }}
            />
          </div>
          <div className="col">
            <button
              type="button"
              className="btn btn-warning mr-2"
              onClick={() => {
                // let valuePrice = this.priceRef.current.value;
                // console.log({ valuePrice });
                // console.log("state", this.state);
                this.handleThemMonAn();
              }}
            >
              Thm mn
            </button>
            <button
              type="button"
              className="btn btn-success ml-2"
              onClick={() => {
                this.handleCapNhatMonAn();
              }}
            >
              Cp nht
            </button>
          </div>
        </form>
      </div>
    );
  }
}

let mapStateToProps = (state) => {
  return {
    foodEdit: state.foodEdit,
  };
};

let mapDispatchToProps = (dispatch) => {
  return {
    capNhatDanhSachMonAn: (danhSach) => {
      dispatch(capNhatDanhSachMonAnAction(danhSach));
    },

    capNhatMonAnMonAn: (id, monAn) => {
      dispatch({
        type: CAP_NHAT_MON_AN,
        id: id,
        payload: monAn,
      });
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FoodForm);
